<script setup>
import { ref, onMounted } from 'vue'
import debounce from 'utils/debounce'
import { useDashboard } from 'store/pinia/dashboard'

import { VueDraggableNext as DashboardContainerDrag } from 'vue-draggable-next'

import DashboardCardItem from './DashboardCardItem.vue'

const store = useDashboard()
const list = ref([])
const limit = ref(10)

const onUpdate = debounce(async () => {
  store.$updatePersonalize({
    limit: limit.value,
    data: list.value.map(i => ({
      id: i.id,
      parent_id: i.parent_id,
      name: i.name
    }))
  })
}, 300)

onMounted(async () => {
  const res = await store.$getPersonalize()
  list.value = res.data || []
  limit.value = res.limit || 10
  if (list.value.length) {
    list.value.map(async (i) => {
      i.isLoading = true
      const res = await store.$getItem(i.id, limit.value)
      i.orders = res?.data?.data?.map(j => ({ ...j, tooltip: false })) || []
      i.totalDevices = res?.data?.total_devices || 0
      i.totalOrders = res?.data?.total || 0
      i.isLoading = false
      i.link = !!i.parent_id
        ? `/${store.buybackType === 'mailin' ? 'order' : 'order-local'}/active/${i.parent_id}/${i.id}`
        : `/${store.buybackType === 'mailin' ? 'order' : 'order-local'}/active/${i.id}`
    })
  }
})

</script>

<template>
  <DashboardContainerDrag
    :list="list"
    handle=".dragable__icon"
    :class="[
      'grid gap-4',
      list.length === 1 && 'grid-cols-1',
      list.length === 2 && 'md:grid-cols-2 grid-cols-1',
      list.length >= 3 && 'md:grid-cols-3 sm:grid-cols-2 grid-cols-1'
    ]"
    @update="onUpdate">
    <transition-group name="slide-fade">
      <div
        v-for="(item, n) in list"
        :key="n"
        class="h-full">
        <DashboardCardItem :item="item">
          <template #header="{ name, total }">
            <div class="flex justify-between">
              <div class="flex items-center gap-x-2">
                <router-link :to="item?.link || '#'" class="hover:underline transition-all ease-out duration-300">
                  <p class="text-xl font-medium text-teal-04">{{ name }}</p>
                </router-link>
                <div class="bg-teal-04 rounded-full px-2 py-0.5 text-sm inline-block text-center text-white flex items-center font-medium">{{ total > 99 ? `99+` : total }}</div>
              </div>
              <a href="#" class="dragable__icon hover:text-teal-04 text-teal-01 transition-all ease-in-out duration-300 cursor-pointer">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                  <path d="M7.08341 4.58341L10.0001 1.66675M10.0001 1.66675L12.9167 4.58341M10.0001 1.66675V18.3334M18.3334 10.0001H1.66675M18.3334 10.0001L15.4167 12.9167M18.3334 10.0001L15.4167 7.08341M1.66675 10.0001L4.58341 7.08341M1.66675 10.0001L4.58341 12.9167M10.0001 18.3334L7.08341 15.4167M10.0001 18.3334L12.9167 15.4167" stroke="currentColor" stroke-width="2"/>
                </svg>
              </a>
            </div>
          </template>
        </DashboardCardItem>
      </div>
    </transition-group>
  </DashboardContainerDrag>
</template>
