<script setup>
import { ref, watch } from 'vue'

import Card from 'bbUi/components/card/card.vue'
import Currency from 'components/Currency.vue'
import ExpeditedIcon from 'components/ExpeditedPayout/ExpeditedIcon.vue'

const props = defineProps({
  item: {
    type: Object,
    default: () => ({
      id: null,
      shortName: null,
      name: null,
      isLoading: true,
      totalDevices: 0,
      totalOrders: 0,
      orders: []
    })
  }
})
const state = ref(props.item)

const onMouseEnter = (e, order) => {
  const el = e.target
  if (el.scrollWidth > el.clientWidth) {
    order.tooltip = true
  } else order.tooltip = false
}
const onMouseLeave = (order) => {
  order.tooltip = false
}

watch(() => props.item, async (val) => {
  state.value = val
})

</script>

<template>
  <Card v-loading="item?.isLoading" class="h-full">
    <template v-if="!$slots.header" #header>
      <div class="flex">
        <div class="flex items-center gap-x-2">
          <router-link to="#" class="hover:underline transition-all ease-out duration-300">
            <p class="text-xl font-medium text-teal-04">{{ state.name }}</p>
          </router-link>
          <div class="bg-teal-04 rounded-full px-2 py-0.5 text-sm inline-block text-center text-white flex items-center font-medium">{{ state.totalOrders }}</div>
        </div>
      </div>
    </template>
    <template v-else #header>
      <slot name="header" v-bind="{ name: state.name, total: state.totalOrders }"></slot>
    </template>
    <template #default>
      <div v-if="state?.orders?.length" class="space-y-2">
        <div
          v-for="(order, index) in state.orders"
          :key="index">
          <router-link
            class="block cursor-pointer transition duration-200 rounded-lg hover:bg-grey-lighten"
            :to="{
              name: order.buyback_type == 1 ? 'buybackOnlineDetail' : 'buybackLocalDetail',
              params: {
                buybackId: order.box_id, box_id_link: order.box_id_link
              }
            }">
            <div class="w-full space-y-1 p-3">
              <div
                class="flex justify-between gap-x-1 relative"
                @mouseleave="onMouseLeave(order)">
                <div class="flex items-center gap-4">
                  <Transition name="fade">
                    <div v-if="order.tooltip" class="absolute bottom-full pb-1">
                      <div
                      :class="!order?.customer_name?.trim() ? 'lowercase' : 'capitalize'"
                      class="bg-teal-04 px-4 py-2 text-sm rounded-lg left-0 text-white">
                      {{ !order?.customer_name?.trim() ? order?.customer?.email : order?.customer_name }}
                      </div>
                    </div>
                  </Transition>
                  <div
                    :class="!order?.customer_name?.trim() ? 'lowercase' : 'capitalize'"
                    class="text-teal-04 font-medium overflow-hidden break-all whitespace-nowrap overflow-ellipsis"
                    @mouseenter="onMouseEnter($event, order)">
                    {{ !order?.customer_name?.trim() ? order?.customer?.email : order?.customer_name }}
                  </div>
                  <div
                    v-if="order?.expedited_payout_price"
                    class="w-max flex items-center gap-2 bg-green-lighten text-green-04 py-1 px-4 rounded-full text-sm"
                    >
                    <ExpeditedIcon class="text-green-04" />
                    <span>Expedited Payout</span>
                  </div>
                </div>
                <p class="font-medium text-teal-04 whitespace-nowrap">
                  <Currency :value="parseFloat(order.sub_total).toFixed(2).toLocaleString()" />
                </p>
              </div>
              <p class="text-dark text-xs">{{ order.order_datetimes }}</p>
            </div>
          </router-link>
        </div>
      </div>
      <div v-else class="flex items-center justify-center h-full">
        <span class="text-teal-02">Nothing to see here</span>
      </div>
    </template>
  </Card>
</template>
