import { defineStore } from 'pinia'
import request from 'utils/request'
import store from 'store'

export const useDashboard = defineStore('dashboard', {
  state: () => ({
    getCard: {
      isLoading: false,
      result: [],
      isLoadingItem: false,
      resultItem: []
    },
    getPersonalize: {
      abortController: null
    }
  }),
  getters: {
    buybackType() {
      return store?.state?.user?.me?.is_location_mail_in === 1 ? 'mailin' : 'instore'
    }
  },
  actions: {
    async $getPersonalize() {
      const response = await request({
        url: '/v1/pos/order-status/dashboard/setting',
        method: 'get',
        params: {
          buyback_type: this.buybackType
        }
      })

      return response
    },
    async $updatePersonalize(payload) {
      if (this.getPersonalize.abortController) {
        this.getPersonalize.abortController.abort()
      }

      const controller = new AbortController()
      this.getPersonalize.abortController = controller

      try {
        await request({
          url: '/v1/pos/order-status/dashboard/setting',
          method: 'put',
          data: {
            ...payload,
            buyback_type: this.buybackType
          },
          signal: controller.signal
        })
  
        this.getPersonalize.abortController = null
      } catch (error) {
        if (error.name === 'AbortError') console.log('Request Canceled!')
          else throw error
      }
    },
    async $getItem(id, limit) {
      const response = await request({
        url: `/v1/pos/dashboard/order/${id}`,
        method: 'get',
        params: {
          month: 'all',
          year: 'all',
          limit: limit,
          buyback_type: this.buybackType === 'mailin' ? 1 : 2
        }
      })

      return response
    }
  }
})
